@keyframes rocket {
  0% {
    transform: rotate(0deg) translateY(0) translateZ(0);
  }

  14% {
    transform: rotate(2deg) translateY(4%) translateZ(0);
  }

  28% {
    transform: rotate(0deg) translateY(8%) translateZ(0);
  }

  42% {
    transform: rotate(-2deg) translateY(4%) translateZ(0);
  }

  56% {
    transform: rotate(0deg) translateY(0) translateZ(0);
  }

  70% {
    transform: rotate(2deg) translateY(-4%) translateZ(0);
  }

  84% {
    transform: rotate(0deg) translateY(-8%) translateZ(0);
  }

  100% {
    transform: rotate(-2deg) translateY(-4%) translateZ(0);
  }
}
